export const RolesContainer = styled.div`
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 100;
  align-items: flex-start;
  @media(max-width: 340px) {
    flex-direction: column;
  }
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click buy to mint your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);
  const [mintAmountFixed, setMintAmountFiexd] = useState(false);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false
  });

  const addTiltImage = () => {
    VanillaTilt.init(document.querySelectorAll('#rolesContainer'))
  };

  const claimNFTs = async () => {
    let markleProof = data.higherProof;
    let cost = data.cost;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = (mintAmount > data.freeMintable ? ((mintAmount - data.freeMintable) * cost) : 0).toString();
    let totalGasLimit = String(gasLimit * mintAmount);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount, markleProof)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then(async (receipt) => {

        swal("Mint succesfull!", "Welcome to the fam!", "success", {
          buttons: {
            opensea: "Check on Opensea!",
            ok: true
          }
        }).then((value) => {
          switch (value) {
            case "opensea":
              window.open(CONFIG.MARKETPLACE_LINK, '_blank').focus();
              break;
            default: break;
          }
        });

        //console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(await fetchData(blockchain.account));
      });
  };

  const setupFixedAmount = () => {
    if (!mintAmountFixed) {
      setMintAmount(data.isOG ? 15 : data.isWL ? 10 : 5)
      setMintAmountFiexd(true)
    }
  }

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > CONFIG.MAX_MINT_AMOUNT) {
      newMintAmount = CONFIG.MAX_MINT_AMOUNT;
    }
    setMintAmount(newMintAmount);
  };

  const getData = async () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(await fetchData(blockchain.account))
    }
  };

  const getGeneralData = () => {
    dispatch(fetchGeneralData())
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
    getGeneralData();

    ethereum.on("accountsChanged", (accounts) => window.location.reload());
    ethereum.on("chainChanged", () => window.location.reload());

  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ paddingTop: 24, backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
      >
        <StyledLogo alt={"logo"} src={"/config/images/collagebot_logo.png"} />
        <s.SpacerSmall />

        <ResponsiveWrapper flex={1} style={{ padding: 24 }} test>
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg alt={"example"} src={"/config/images/example.gif"} />
          </s.Container>
          <s.SpacerMedium />

          <s.Container
            flex={3}
            jc={"center"}
            ai={"center"}
            style={{
              //backgroundColor: "var(--accent)",
              backgroundColor: "#310e68",
              backgroundImage: "linear-gradient(316deg, #320a68 0%, #5a0f40 74%)",
              padding: 25,
              borderRadius: 15,
              border: "1px solid var(--secondary)",
              //boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
              boxShadow: "rgba(0, 0, 0, 0.65) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px",
              height: "120%",
              margin: "auto",
            }}
          >

            <RolesContainer
              id="rolesContainer"
              style={{ transformStyle: "preserve-3d" }}>
              <RolesImg
                alt={"mintRole"}
                src={"/config/images/public.png"}
                style={{ transform: "scaleX(1)", marginLeft: "auto" }}
              />
              <RolesImg
                alt={"mintRole"}
                src={"/config/images/whitelist.png"}
                style={{ transform: "scaleX(1)", marginRight: "11%", marginLeft: "11%" }}
              />
              <RolesImg
                alt={"mintRole"}
                src={"/config/images/og-role.png"}
                style={{ transform: "scaleX(1)", marginRight: "auto" }}
              />
              {addTiltImage()}
            </RolesContainer>

            <s.SpacerMedium />

            <s.TextTitle
              style={{
                textAlign: "center",
                fontSize: 50,
                fontWeight: "bold",
                color: "var(--accent-text)",
              }}
            >
              {data.totalSupply} / {data.maxSupply}
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
              }}
            >
              <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
              </StyledLink>
            </s.TextDescription>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= Number(data.maxSupply) ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  1 {CONFIG.SYMBOL} costs {CONFIG.DISPLAY_COST}{" "}
                  {CONFIG.NETWORK.SYMBOL} <span style={{ fontSize: "80%" }}>(Excluding gas </span>
                  <span style={{ fontSize: "80%" }}
                    onClick={(e) => {
                      e.preventDefault();
                      dispatch(connect());
                      getData()
                    }}>fees)</span>
                </s.TextTitle>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                  blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Connect to the {CONFIG.NETWORK.NAME} network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      disabled
                      style={{ backgroundColor: "#292929", opacity: "0.3", boxShadow: "rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset" }}
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData()
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}

                    <s.SpacerMedium />

                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>

                    <s.SpacerMedium />

                    {data.freeMintable != undefined ?

                      <>
                        <ContainerMintSection onLoad={setupFixedAmount()}>
                          {data.freeMintable > 0
                            ? <s.Container style={{ textAlign: "center" }}>
                              <RolesContainer>
                                {data.isOG
                                  ? <RolesImg alt={"example"} src={"/config/images/og-role_label.png"} style={{ margin: "auto" }} />
                                  : data.isWL
                                    ? <RolesImg alt={"example"} src={"/config/images/whitelist_label.png"} style={{ margin: "auto" }} />
                                    : data.isWHALE
                                      ? <s.TextTitle
                                        style={{ margin: "auto", fontSize: "2em", fontWeight: "800", textAlign: "center", color: "var(--accent-text)" }}>
                                        WHALE ROLE
                                      </s.TextTitle>
                                      : <RolesImg alt={"example"} src={"/config/images/public_label.png"} style={{ margin: "auto" }} />
                                }
                              </RolesContainer>
                              <s.TextTitle
                                style={{ margin: "auto", fontSize: "2em", fontWeight: "800", textAlign: "center", color: "var(--accent-text)" }}
                              >
                                {data.freeMintable} FREE
                              </s.TextTitle>
                              <s.SpacerMedium />
                            </s.Container>
                            : ""}

                          {!claimingNft ?
                            <>
                              <s.Container>
                                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                                  <StyledRoundButton
                                    style={{ lineHeight: 0.4 }}
                                    disabled={claimingNft ? 1 : 0}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      decrementMintAmount();
                                    }}
                                  >
                                    -
                                  </StyledRoundButton>
                                  <s.SpacerMedium />
                                  <s.TextDescription
                                    style={{
                                      textAlign: "center",
                                      color: "var(--accent-text)",
                                    }}
                                  >
                                    {mintAmount}
                                  </s.TextDescription>
                                  <s.SpacerMedium />
                                  <StyledRoundButton
                                    disabled={claimingNft ? 1 : 0}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      incrementMintAmount();
                                    }}
                                  >
                                    +
                                  </StyledRoundButton>
                                </s.Container>

                                <s.SpacerSmall />
                                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                                  <StyledButton
                                    disabled={claimingNft ? 1 : 0}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      claimNFTs();
                                      getData();
                                    }}
                                  >
                                    {claimingNft ? "BUSY" : "BUY"}{data.hexProofOG != null}
                                  </StyledButton>
                                </s.Container>
                              </s.Container>
                            </>
                            :
                            <s.Container ai={"center"} jc={"center"} fd={"row"}>
                              <SpinnerCircular />
                            </s.Container>}

                          <>
                            <s.SpacerSmall />
                            <s.TextTitle style={{ textAlign: "center", color: "var(--accent-text)" }} >
                              MINT COST: {
                                Web3.utils.fromWei(
                                  (mintAmount > data.freeMintable ? ((mintAmount - data.freeMintable) * data.cost) : 0).toString(), 'ether')
                              }
                            </s.TextTitle>
                          </>

                        </ContainerMintSection>
                      </>
                      : <SpinnerCircular />}
                  </>
                )}
              </>
            )}
          </s.Container>

          <s.SpacerMedium />
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg
              alt={"example"}
              src={"/config/images/example2.gif"}
              style={{ transform: "scaleX(-1)" }}
            />
          </s.Container>
        </ResponsiveWrapper>

        <s.SpacerLarge />
        <s.Container jc={"center"} ai={"center"} style={{ textDecoration: "none", textAlign: "center", display: "inline", width: "70%" }}>
          <a className="socialIconLink" href="https://twitter.com/CollageBotsNFT" style={{ fontSize: "30px" }}>
            <FaTwitter />
          </a>
          <a className="socialIconLink" href="https://discord.gg/UFEkYyHJGY" style={{ fontSize: "30px", marginLeft: "8px" }}>
            <FaDiscord />
          </a>
          <s.SpacerSmall />
        </s.Container>

        <s.SpacerMedium />
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            Please make sure you are connected to the right network (
            {CONFIG.NETWORK.NAME} Mainnet) and the correct address. Please note:
            Once you make the purchase, you cannot undo this action.
          </s.TextDescription>
          <s.SpacerSmall />
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            TESTO COLLAGE BOT - PRE-MINTED DIGITAL ART RIGHT RESERVED - WEBSITE RIGHTS RESERVED
          </s.TextDescription>
        </s.Container>
      </s.Container >
    </s.Screen >
  );
}

export default App;
